.icon {
  font-size: 20px;
  margin-right: 2rem;
}

.footer {
  margin-top: 10vh;
  width: 100%;
  padding: 6rem 0;
  background: rgba(0, 0, 0, 0.8);
}

.footer-container {
  max-width: 1140px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
}

.footer .left {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.footer .right {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin: auto;
}

.footer h4 {
  font-size: 1.4rem;
  padding: 1rem 0;
}

.footer p {
  font-size: 1.2rem;
  padding: 1rem 0;
  text-align: justify;
}

.footer .location {
  display: flex;
  align-items: center;
}

.footer .location p {
  padding-bottom: 0.5rem;
}

.footer .location h4 {
  padding: 0;
}
.icon {
  transition: transform 0.3s ease-in-out;
}
.icon:hover {
  transform: scale(1.3);
}

@media screen and (max-width: 640px) {
  .footer-container {
    grid-template-columns: 1fr;
  }
  .right .social {
    margin: auto;
  }
}
